.header {
	// .header__top
	&__top {
	}
}
.top-header {
	// .top-header__container
	&__container {
		height: 100px;
		@include adaptiveValue("padding", 70, 15);
		padding: rem(23) rem(70) rem(23) rem(98);
		display: flex;
		align-items: center;
		position: relative;
		border-bottom: 1px solid rgb(255, 255, 255, 0.1);
		@media (max-width: $mobile) {
			height: 142px;
			align-items: start;
			padding: rem(12) rem(15) rem(12) rem(15);
		}
	}
	// .top-header__menu
	&__menu {
	}
	// .top-header__logo
	&__logo {
		font-weight: 700;
		font-size: 25px;
		letter-spacing: 0.15em;
		color: #ffffff;
		z-index: 4;
		@include adaptiveValue("margin-right", 72, 10);
		@media (max-width: $mobile) {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
		}
	}
	// .top-header__form
	&__form {
	}
	// .top-header__contact
	&__contact {
		margin-right: 10px;
		@media (max-width: $mobile) {
			display: none;
		}
	}
	&__signin {
		display: flex;
		align-items: center;
		position: relative;
		padding-left: 21px;
		margin: 0 rem(22);
		&::before {
			content: "";
			display: block;
			width: 1px;
			height: 52px;
			background: #635c5a;
			position: absolute;
			top: -11px;
			left: 0;
			@media (max-width: $mobile) {
				display: none;
			}
		}
		@media (max-width: $mobile) {
			margin: 20px 0px;
			padding-left: 0px;
		}
		span {
			font-weight: 500;
			font-size: 18px;
			line-height: 21px;
			color: #fff;
			margin-left: 10px;
		}
	}
	// .top-header__btn
	&__btn {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0 0 0 auto;
		width: 149px;
		height: 52px;
		background: linear-gradient(114.93deg, #618967 5.11%, #72a479 94%);
		border-radius: 10px;
		color: #ffffff;
		font-weight: 600;
		font-size: 14px;
		padding: rem(7) rem(14) rem(7) rem(24);
		position: relative;
		transition: all 0.3s ease 0s;
		&:hover {
			background: linear-gradient(114.93deg, #88af8e 5.11%, #3b8845 94%);
		}
		@media (max-width: $mobile) {
			position: absolute;
			top: 10px;
			right: 15px;
			background: #618967 url("../../assets/img/icons/Buy.svg") center 3px no-repeat;
			flex-direction: column;
			justify-content: flex-end;
			align-items: center;
			padding: rem(7) rem(7) rem(7) rem(7);
			text-transform: lowercase;
			width: 62px;
			height: 56px;
			z-index: 4;
		}
		&::before {
			content: "";
			position: absolute;
			background-color: #fff;
			width: 1px;
			height: 35px;
			top: 7px;
			right: 32%;
			@media (max-width: $mobile) {
				height: 1px;
				width: 48px;
				top: 31px;
				right: 8px;
			}
		}
		span {
			display: block;
			background: #ffffff;
			border-radius: 50%;
			color: #403c3b;
			padding: 3px 7px 3px 7px;
			margin-left: 20px;

			@media (max-width: $mobile) {
				display: none;
			}
		}
	}
}

.form-header {
	input,
	button {
		border: none;
		outline: none;
		border-radius: 10px;
	}
	width: 555px;
	margin-right: 48px;

	&__container {
		position: relative;
		@media (max-width: $mobile) {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			border: 15px solid transparent;
		}
	}
	// .form-header__input
	&__input {
		color: #fff;
		font-size: 18px;
		width: 100%;
		height: 52px;
		padding-left: 48px;
		background: #504b4a url("../../assets/img/icons/Location.svg") no-repeat 14px 50%;
		&::placeholder {
			font-weight: 400;
			font-size: 18px;
			color: #cfcfcf;
		}
		&:focus {
			outline: none;
			box-shadow: inset 0 1px 3px 1px rgba(0, 0, 0, 0.5), inset 0 0 0 60px rgba(0, 0, 0, 0),
				0 1px rgba(255, 255, 255, 0.08);
		}
	}

	// .form-header__button
	&__button {
		position: absolute;
		padding-right: 14px;
		top: 14px;
		right: 8px;
		background: #504b4a;
		cursor: pointer;
		&::before {
			content: url("../../assets/img/icons/Search.svg");
		}
	}
}
.contact-tel {
	padding-left: 42px;
	background: url("../../assets/img/icons/phone.png") 0 center no-repeat;
	vertical-align: center;
	white-space: nowrap;
	// .contact-tel__icon
	&__icon {
		color: #fff;
		font-size: 16px;
	}
	// .contact-tel__phone
	&__phone {
		vertical-align: c;
		display: block;
		font-weight: 700;
		font-size: 18px;
		line-height: 20px;
		color: #fff;
		span {
			font-weight: 400;
			font-size: 14px;
			line-height: 20px;
			color: #cfcfcf;
		}
	}
}

