//==============================================================================
.menu {
	display: flex;
	justify-content: space-around;
	align-items: center;
	display: none;
	@media (max-width: 767px) {
		display: block;
	}
}
.header-menu {
	display: flex;
	align-items: center;
	justify-content: space-around;
	width: 100%;
	gap: 15px;
	padding: 0;
	margin: 0;
	@media (max-width: 767px) {
		transform: translateX(-150%);
	}

	// .header-menu__item
	&__item {
		list-style: none;
		a {
			text-decoration: none;
			color: white;
			font-size: 20px;
			transition: all 0.3s ease 0s;
			white-space: nowrap;
			&:hover {
				text-decoration: underline;
			}
		}
	}
}
._active {
	@media (max-width: 767px) {
		position: fixed;
		overflow: auto;
		display: block;
		width: 100%;
		height: 100%;
		transition: all 0.3s ease 0s;
		padding: 100px 20px 20px 20px;
		top: 0;
		left: 0;
		background-color: rgb(67 59 59);
		z-index: 2;
		transform: translateX(0);
		li {
			margin-bottom: 40px;
		}
	}
}

//burger-icon
.icon-menu {
	display: none;
	@media (max-width: 767px) {
		display: flex;
		flex-direction: column;
		cursor: pointer;
		z-index: 5;
		background-color: inherit;
		border: none;
		outline: none;
		z-index: 10;
		span {
			transition: all 0.3s ease 0s;
			width: 32px;
			height: 4px;
			margin: 4px;
			border-radius: 2px;
			background-color: rgb(255, 255, 255, 0.5);
			z-index: 10;
		}

		.open:nth-child(1) {
			transform: rotate(45deg) translateY(16px);
			width: 44px;
		}
		.open:nth-child(2) {
			opacity: 0;
		}
		.open:nth-child(3) {
			transform: rotate(-45deg) translateY(-16px);
			width: 44px;
		}
	}
}
